@import "mixins";



.header {
    padding: 0 2em;
    @include flex(space-between);
    height: $navbar_height;

    .iconContainer {
        @include flex(space-evenly, center, 3em);
    }
    .navigation {
        @include flex(space-evenly, center, 3em);

        div {
            cursor: pointer;
        }

        li {
            display: inline;
            cursor: pointer;
            padding: 20px 20px;
            font-size: large;
        }

        button {
            cursor: pointer;
            color: rgb(0, 0, 0);
            background-color: #817aa1;
            border-radius: 5px;
            box-shadow: 0 2px 2px 1px #0f0f0f;
            font-family: inherit;
            padding: 1em;

            &:hover {
                background-color: #4e476e;
            }
        }
    }
}